import React from "react";
import SectionTitle from "./SectionTitle";
import ServiceItem from "./ServicesItem";
import web from "../../assets/mainServices/web-01.svg";
import mobile from "../../assets/mainServices/mobile-01.svg";
import graphic from "../../assets/mainServices/graphicDesign-01.svg";

const ProjectsSection = () => {
  return (
    <div className="">
    <div className="">
    <SectionTitle
        id="03"
        title="أعمالنا"
        subtitle="ماذا نقدم"
        description="من الحكمة أن لا نمارس أي تمرين من خلال ممارسة التمارين الرياضية التي
          نمارسها."
      />
    </div>

      <div className="grid lg:grid-cols-2 justify-center  gap-4 lg:gap-8 px-8 sm:px-24 lg:px-52 ">
      <div className="lg:col-span-2">
      <ServiceItem name="برمجة مواقع" icon={web} description="برمجة الويب (Web Development) هي عبارة عن عملية بناء وتطوير مواقع
              وتطبيقات على الإنترنت. تشمل هذه العملية عملية بناء وتصميم الواجهات
              الأمامية للمواقع والتطبيقات وتطوير المحتوى والدعم الفني لها."/>
      </div>
        
        
        <ServiceItem name="تطبيقات موبايل" icon={mobile} description="برمجة تطبيقات الموبايل هي عملية بناء وتطوير تطبيقات للهاتف المحمول والجوال. تشمل هذه العملية عملية بناء وتصميم الواجهات الأمامية للتطبيقات وتطوير المحتوى والدعم الفني لها."/>
        <ServiceItem name="غرافيك ديزاين" icon={graphic} description="
ديزاين غرافيكي هو عملية بناء وتصميم وتطوير الواجهات البصرية للمواقع والتطبيقات والمنتجات والخدمات والملفات التعريفية للشركات وغيرها."/>
        
        
      </div>
    </div>
  );
};

export default ProjectsSection;
